<template>
  <div id="main-app">
    <v-navigation-drawer
      app
      permanent
      width="220"
    >
      <v-layout
        column
        justify-start
        class="app-nav"
        fill-height
      >
        <v-layout
          justify-center
          align-center
          class="text-xs-center text-xs-center cyan py-3"
          v-ripple
          column
          @click="$router.push('/')"
        >
          <v-avatar tile size="160">
            <v-img src="/img/logo.png" />
          </v-avatar>
        </v-layout>
        <v-flex fill-height>
          <v-list>
            <template v-for="(menu, idx) in menus">
              <v-list-tile
                :key="idx"
                avatar
                :to=menu.to
              >
                <v-list-tile-avatar>
                  <font-awesome-icon
                    :icon=menu.icon
                    class="gray--text"
                  ></font-awesome-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>
                    {{ menu.text }}
                  </v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider :key="`menu-divider-${idx}`"/>
            </template>
            <v-list-tile
              avatar
              @click="logout"
            >
              <v-list-tile-avatar>
                <font-awesome-icon
                  icon="power-off"
                  class="gray--text"
                ></font-awesome-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>
                  Logout
                </v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-flex>
      </v-layout>
    </v-navigation-drawer>

    <router-view/>

    <!-- Application Loading Pane -->
    <v-container fluid fill-height v-if="loading" class="app-loader">
      <v-layout fill-height justify-center align-center>
        <v-progress-circular
          width="16"
          size="126"
          color="indigo"
          indeterminate
        >
        </v-progress-circular>
      </v-layout>
    </v-container>

    <intercept-action/>
  </div>
</template>

<script>
import Vue from "vue";
import evbus from "@/evbus";
import InterceptAction from "./InterceptAction";
import auth from "@/services/auth";

export default Vue.extend({
  name: "MainApp",
  components: {
    InterceptAction,
  },
  data: () => ({
    loading: false,
    menus: [
      {
        text: "Home",
        icon: "home",
        to: "/"
      },
      {
        text: "Data",
        icon: "table",
        to: "/app/data"
      },
      {
        text: "Aturan",
        icon: "braille",
        to: "/app/aturan"
      },
      {
        text: "Prediksi",
        icon: "atom",
        to: "/app/prediksi"
      },
      {
        text: "Pengujian",
        icon: "biking",
        to: "/app/mape"
      }
    ]
  }),
  methods: {
    logout() {
      auth.logout()
      this.$router.push("/")
    }
  },
  mounted() {
    if (!auth.checkUser()) {
      this.$router.push('/')
    }
    evbus.$on("loading-on", () => {
      this.loading = true;
    });
    evbus.$on("loading-off", () => {
      this.loading = false;
    });
  },
  beforeDestroy() {
    evbus.$off("loading-on");
    evbus.$off("loading-off");
  }
});
</script>

<style lang="scss">
  #main-app {
    .app-nav {
    }

    .app-loader {
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: rgba(255, 255, 255, 0.9);
      z-index: 999;
    }

    table.v-table thead th {
      font-weight: bold !important;
      font-size: 14px;
    }
  }
</style>
